.instagram {
  overflow: hidden;
  position: relative;
  display: block;
}

.instagram a {
  color: #fff;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease-in-out;
  opacity: 0;
  z-index: 99;
}

@media (max-width: 991px) {
  .instagram a {
    opacity: 1;
  }
}

.instagram a i {
  padding-right: 10px;
}

