    display: none;
  }
}

@media (max-width: 575px) {
  .instagram-item-thum:nth-child(2) {
    display: none;
  }
}

@media (max-width: 575px) {
  .instagram-item-thum:nth-child(3) {
    display: none;
  }
}

.instagram-item-thum img {
  width: 100%;
}

.instagram::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #080808b3;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  z-index: 99;
}

@media (max-width: 991px) {
